@import "../../_globalColor";

.main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 4rem;
}
.greet-main {
  width: 100%;
  padding: 0;
  margin: 0;
  min-height: calc(100vh - 80px);
  display: flex;
  align-items: center;
  position: relative;
  background: linear-gradient(135deg, rgba(107, 91, 149, 0.03) 0%, rgba(102, 126, 234, 0.03) 100%);
}

.greeting-main {
  display: flex;
  width: 90%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 40px 20px;
}

.subTitle {
  color: $subTitle !important;
}

.greeting-main {
  display: flex;
}

.greeting-main > * {
  flex: 1;
  margin-bottom: 30px;
}

.empty-div {
  margin-bottom: 40px;
}

.button-greeting-div {
  display: flex;
  margin-top: 40px;
}
.download-link-button {
  text-decoration: none;
  
  &.main-button {
    display: inline-block;
    padding: 14px 28px;
  }
}

.greeting-text {
  font-size: 70px;
  line-height: 1.1;
  color: $textColor !important;
}

.greeting-text-p {
  font-size: 30px;
  line-height: 40px;
}

.greeting-image-div > img {
  max-width: 100%;
  height: auto;
}

.wave-emoji {
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  animation-name: wave;
  display: inline-block;
  transform-origin: 70% 70%;
}

@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.rotating-role-container {
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
}

.role-label {
  color: $textColor;

  &.dark-mode {
    color: $textColorDark;
  }
}

/* Media Query */
@media (max-width: 1380px) {
  .greeting-text {
    font-size: 50px;
  }
  .greeting-text-p {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .greet-main {
    min-height: calc(100vh - 60px);
  }

  .greeting-main {
    padding: 30px 20px;
  }

  .button-greeting-div {
    justify-content: space-around;
    flex-direction: column;
    gap: 15px;
  }
  .greeting-text {
    font-size: 30px;
    text-align: center;
  }
  .greeting-text-p {
    font-size: 16px;
    line-height: normal;
    text-align: center;
  }
  .greeting-main {
    display: block;
  }
  .greeting-image-div {
    height: 256px;
    margin-top: 30px;
  }
  .rotating-role-container {
    font-size: 18px;
    justify-content: center;
  }
}

@media (max-width: 320px) {
  .main {
    width: auto;
  }
}
